.per-work .per-work-wrapper {
  position: relative;
  overflow: hidden;
  height: auto; }
  .per-work .per-work-wrapper .per-work-anchor {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
  .per-work .per-work-wrapper .per-work-img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; }
    .per-work .per-work-wrapper .per-work-img img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .per-work .per-work-wrapper .per-work-title {
    position: absolute;
    right: 0;
    bottom: -1px;
    left: 0;
    padding-top: 5rem;
    padding-right: 2rem;
    padding-bottom: 1.5rem;
    padding-left: 2rem;
    z-index: 1;
    display: flex;
    flex-direction: column;
    transition-duration: 0.45s;
    transform: scale(1) translate(0%, 0%);
    opacity: 0;
    background: linear-gradient(transparent, rgba(30, 30, 30, 0.8));
    color: white; }
    .per-work .per-work-wrapper .per-work-title h2 {
      font-size: 1.2em;
      margin: 0 0 10px;
      font-family: "Montserrat", sans-serif;
      transition-duration: 0.35s;
      transform: scale(1) translate(0%, 35%); }
    .per-work .per-work-wrapper .per-work-title .per-work-tags {
      font-family: "Open Sans", sans-serif;
      font-weight: 300;
      text-transform: uppercase;
      font-size: 0.685em;
      margin: 0;
      text-align: left;
      line-height: 1.2;
      transition-duration: 0.35s;
      transform: scale(1) translate(0%, 100%);
      opacity: 0; }
  .per-work .per-work-wrapper:before {
    display: block;
    content: " ";
    padding-bottom: 47%; }
  .per-work .per-work-wrapper:after {
    content: '';
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: rgba(0, 0, 0, 0.15);
    position: absolute; }
  .per-work .per-work-wrapper:hover .per-work-title {
    transform: scale(1) translate(0%, 0%);
    opacity: 1; }
    .per-work .per-work-wrapper:hover .per-work-title h2 {
      transform: scale(1) translate(0%, 0%);
      opacity: 1; }
    .per-work .per-work-wrapper:hover .per-work-title .per-work-tags {
      transform: scale(1) translate(0%, 0%);
      opacity: 0.75; }
