.main-container .work-container {
  justify-content: flex-start; }
  .main-container .work-container h1 {
    background: -webkit-linear-gradient(#0164ec, #0471ec);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .main-container .work-container .col-11 {
    margin: 0px; }
  .main-container .work-container .work-content-wrapper {
    margin: 2em; }
  .main-container .work-container .work-description {
    max-width: 700px; }

.main-container .work-content-portfolio {
  margin: 10px 0 15px;
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 15px; }

@media only screen and (min-width: 400px) {
  .main-container .work-content-portfolio {
    margin: 20px 0 25px;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 15px;
    grid-row-gap: 20px; } }

@media only screen and (min-width: 576px) {
  .main-container .work-content-portfolio {
    grid-column-gap: 15px;
    grid-row-gap: 20px; } }

@media only screen and (min-width: 768px) {
  .main-container .work-container {
    justify-content: flex-start; }
    .main-container .work-container h1 {
      background: -webkit-linear-gradient(#0164ec, #0471ec);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }
    .main-container .work-container .col-11 {
      width: 100%;
      margin: 60px; }
    .main-container .work-container .work-description {
      max-width: 700px; }
  .main-container .work-content-portfolio {
    margin: 30px 0 60px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30px;
    grid-row-gap: 50px; } }
