@import url("https://fonts.googleapis.com/css?family=Montserrat:700,700i|Open+Sans:400,400i,700,700i");
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 10px;
  line-height: 1.5;
  color: #5e5e5e; }

html,
body,
#root,
#page {
  position: relative;
  width: 100%;
  min-width: 165px; }

* {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 15px; }

p {
  margin: 15px 0; }

input {
  -webkit-appearance: none; }

button {
  cursor: pointer;
  outline: none; }

a {
  text-decoration: none;
  transition: all 0.15s ease-in-out; }

.link-wrapper {
  text-align: left; }

.link {
  font-size: 0.8em;
  color: white;
  font-weight: 400;
  background: -webkit-linear-gradient(#0471ec, #0164ec);
  display: inline-block;
  padding: 9px 20px;
  border-radius: 3px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3); }
  .link:hover {
    background: -webkit-linear-gradient(#0164ec, #0471ec); }

@media (min-width: 768px) {
  html,
  body,
  #root,
  #page {
    height: 100%; }
  .link-wrapper {
    text-align: center; } }

@media (min-width: 992px) {
  body {
    font-size: 14px;
    line-height: 1.5; } }
